<template>
	<b-modal id="DateRangeModal" dialog-class="full_modal no_static_height" :show="true" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("customDateRange") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="body mb-3 my-5">
					<form-select :label="$t('dateRangeType')" v-model="dateRange.type" :options="dateRangeTypeOptions" required class="mb-5">
						<BIcon slot="icon_left" icon="card-list" font-scale="1.2" />
					</form-select>

					<form-input v-if="dateRange.type == 'days'" required :label="$t('rollingDays')" v-model.number="days.value" v-vuelidate.noDirty="days.value" :errorMsg="days.errorMsg">
						<BIcon slot="icon_left" icon="calendar4-range" font-scale="1.4" style="top: 7px" />
						<template #end_of_input>
							<span class="text-muted position-absolute" :style="`${$isRtl ? 'left' : 'right'}:2px; bottom: 3px; z-index: 1; user-select: none; pointer-events: none`">{{
								$t("days")
							}}</span>
						</template>
					</form-input>

					<template v-if="dateRange.type == 'specific'">
						<virtual-input :label="$t('startAndEndDates')" class="mb-3" :inputProps="{ class: 'cursor-auto' }">
							<BIcon slot="icon_left" icon="calendar4-range" style="top: 7px" />
							<span slot="placeholder" class="text-dark fs-md">{{ startAndEndDatesStr }}</span>
						</virtual-input>

						<date-picker
							is-range
							v-model="range"
							:min-date="minDate"
							is-expanded
							class="border-0"
							title-position="left"
							:locale="{ id: $i18n.locale, firstDayOfWeek: 2 }"
						></date-picker>
					</template>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
					<b-btn type="submit" variant="primary" :disabled="$v.$invalid">{{ $t("save") }}</b-btn>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import FormSelect from "@/components/inputs/FormSelect";
	import VirtualInput from "@/components/inputs/VirtualInput";
	import DatePicker from "v-calendar/lib/components/date-picker.common";

	import { validationMixin } from "vuelidate";

	import { between, requiredIf, numeric } from "vuelidate/lib/validators";

	export default {
		components: { FormInput, FormSelect, DatePicker, VirtualInput },

		props: { value: { type: Object } },

		mixins: [validationMixin],

		data() {
			return {
				dateRange: { type: "days", start: "", end: "" },
				days: { value: 7, isInvalid: false, errorMsg: "" },
				minDate: this.$moment().utc().startOf("date").toDate(),
				isShown: false,
			};
		},

		watch: {
			"dateRange.type"(newValue, oldValue) {
				if (oldValue == "indefinite") {
					this.range = { start: this.dateRange.start, end: this.dateRange.start.clone().add(6, "d") };
				}
			},
			"days.value"(value) {
				let v = this.$v.days.value;

				if (!v.required) {
					this.days.isInvalid = true;
					this.days.errorMsg = this.$t("required");
					return;
				}

				if (!v.numeric) {
					this.days.isInvalid = true;
					this.days.errorMsg = this.$t("mustInteger");
					return;
				}

				if (!v.between) {
					this.days.isInvalid = true;
					this.days.errorMsg = this.$t("mustBetween", [v.$params.between.min, v.$params.between.max]);
					return;
				}

				this.days.isInvalid = false;

				this.days.errorMsg = "";

				if (this.isShown) {
					this.dateRange.start = this.$moment().utc().startOf("date");

					this.dateRange.end = this.$moment().utc().startOf("date").add(value, "days");
				}
			},
		},

		computed: {
			dateRangeTypeOptions() {
				return this.$store.state.invites.dateRangeTypeOptions;
			},

			startAndEndDatesStr() {
				let { start, end } = this.dateRange;

				const isSameYear = (date) => date.year() === this.$moment().year();

				start = start.format(`MMM D${isSameYear(start) ? "" : ", YYYY"}`);

				end = end.format(`MMM D${isSameYear(end) ? "" : ", YYYY"}`);

				if (start === end) return start;

				return `${start} - ${end}`;
			},

			range: {
				get() {
					return { start: this.dateRange.start.toDate(), end: this.dateRange.end.toDate() };
				},
				set(val) {
					this.dateRange.start = this.$moment(val.start).utc().startOf("date");
					this.dateRange.end = this.$moment(val.end).utc().startOf("date");
				},
			},
		},

		validations: {
			days: {
				value: {
					required: requiredIf(function (nestedModel) {
						return nestedModel.type === "days";
					}),
					numeric,
					between: between(1, 1000),
				},
			},
		},

		methods: {
			save() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				let dateRange = { ...this.dateRange };

				if (dateRange.type == "indefinite") {
					dateRange.start = this.$moment().utc().startOf("date");
					dateRange.end = null;
				} else if (dateRange.type == "specific") {
					dateRange.end = dateRange.end.clone().add(1, "d");
				}

				this.$emit("submit", dateRange);

				this.$emit("input", dateRange);

				this.$bvModal.hide("DateRangeModal");
			},

			shown() {
				let { start, end, type } = this.value;

				this.dateRange = { start, end, type };

				if (type == "indefinite") {
					this.days.value = 7;
				} else {
					this.days.value = end.diff(start, "days");
					this.dateRange.end = this.dateRange.end.clone().subtract(1, "d");
				}

				this.$nextTick(() => {
					this.isShown = true;
				});
			},

			reset() {
				this.isShown = false;
				this.$bvModal.hide("DateRangeModal");
			},
		},
	};
</script>

