<template>
	<b-modal id="DurationModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("customEventDuration") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<b-form @submit.prevent="save">
				<div class="body mb-3 my-5">
					<form-input required placeholder="30min" :label="$t('eventDuration')" v-model="duration.value" v-vuelidate.noDirty="duration.value" :errorMsg="duration.errorMsg">
						<BIcon slot="icon_left" icon="clock-history" font-scale="1.4" style="top: 7px" />
						<template #end_of_input>
							<span class="text-muted position-absolute" :style="`${$isRtl ? 'left' : 'right'}:2px; bottom: 3px; z-index: 1; user-select: none; pointer-events: none`">
								{{ $t("minutes") }}
							</span>
						</template>
					</form-input>
				</div>
				<div class="footer d-flex align-items-center justify-content-end">
					<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
					<b-btn type="submit" variant="primary" :disabled="$v.$invalid">{{ $t("save") }}</b-btn>
				</div>
			</b-form>
		</template>
	</b-modal>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";

	import { validationMixin } from "vuelidate";

	import { between, required, numeric } from "vuelidate/lib/validators";

	export default {
		components: { FormInput },

		props: { value: { type: [String, Number] } },

		mixins: [validationMixin],

		data() {
			return {
				duration: {
					value: this.value,
					isInvalid: false,
					errorMsg: "",
				},
			};
		},

		watch: {
			"duration.value"() {
				let v = this.$v.duration.value;

				if (!v.required) {
					this.duration.isInvalid = true;
					this.duration.errorMsg = "Required";
					return;
				}

				if (!v.numeric) {
					this.duration.isInvalid = true;
					this.duration.errorMsg = "Must be an integer";
					return;
				}

				if (!v.between) {
					this.duration.isInvalid = true;
					this.duration.errorMsg = "Must be between 1 and 720";
					return;
				}

				this.duration.isInvalid = false;
				this.duration.errorMsg = "";
			},
		},

		mounted() {
			this.duration.value = this.value;
		},

		validations: {
			duration: {
				value: {
					required,
					numeric,
					between: between(1, 720),
				},
			},
		},

		methods: {
			save() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$emit("input", this.duration.value);

				this.$bvModal.hide("DurationModal");
			},

			shown() {
				this.duration.value = this.value;
			},

			reset() {
				this.$bvModal.hide("DurationModal");
			},
		},
	};
</script>

<style></style>
